h2:after, h2:visited:after {
  content: '';
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  background: rgb(231, 60, 7);
  transition: 0.2s;
}
h2:hover:after, h2:visited:hover:after {
  width: 100%;

}
h2.link, h2:visited.link {
  display: block;
  /* padding: 0.5em; */
}
